body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  margin-left: 20px;
  border-bottom: 1px solid silver;
  font-weight: 700;
  font-size: 45px;
  height: 100px;
}

.tarifs {
  display: flex;
  margin-left: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.block {
  width: 400px;
  border-bottom: 1px solid silver;
  height: 180px;
  position: relative;
}

.hit {
  width: 150px;
  position: absolute;
  top: -30px;
  right: 20px;
}

.price {
  font-weight: 650;
  font-size: 60px;
  color: #1F2229;
}

.right {
  display: inline-block;
}

.rubleSign {
  margin-left: 5px;
  font-weight: 650; 
  font-size: 25px;
  color: #1F2229;
}

.slashTime {
  font-size: 18px;
  font-weight: 500;
  text-transform: lowercase;
}

.internetAmount {
  font-weight: 700;
  font-size: 25px;
}

.internetMemory {
  font-weight: 450;
  padding: 5px;
  font-size: 25px;
}

.socialIcons {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.icon {
  width: 35px;
  margin: -3px;
}

.callMinutesAmount {
  font-size: 29px;
  font-weight: 700;
}

.callMinutes {
  font-weight: lighter;
  font-size: 26px;
}

.callUnlimit {
  font-weight: 500;
  font-size: 20px;
}

.smsAmount {
  font-weight: 700;
  font-size: 25px;
}

.smsText {
  font-weight: 400;
  margin-left: 5px;
}

